.container {
	@include container;
}
.social {
	@include bg;
	&__title {
		display: flex;
		align-items: center;
		margin-left: 160px;
		min-height: 280px;
		font-family: Forum;
		font-size: 48px;
		line-height: 53px;
		letter-spacing: 0.03em;
		color: #ffffff;
		@media (max-width: 768px) {
			margin-left: 15px;
		}
		@media (max-width: 576px) {
			min-height: 200px;
			line-height: 45px;
			font-size: 40px;
		}
	}
	&__bottom {
		min-height: 215px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 160px 0 150px;
		@media (max-width: 768px) {
			padding: 0 15px;
		}
		@media (max-width: 576px) {
			min-height: 150px;
		}
		.messenger-icon {
			&__item {
				margin: 0 10px;
			}
		}
		&-slider {
			display: flex;
			align-items: baseline;
			@media (max-width: 576px) {
				display: none;
			}
			.slick {
				&-arrow {
					z-index: 10;
					@include no-btn;
				}
				&-prev {
					left: 0;
					background: url(../../img/slider/icon/prevArrow.svg) 0 0 / 100% no-repeat;
					width: 51px;
					height: 29px;
					order: 1;
				}
				&-next {
					right: 0;
					background: url(../../img/slider/icon/nextArrow.svg) 0 0 / 100% no-repeat;
					width: 51px;
					height: 29px;
					order: 3;
				}
				&-dots {
					display: flex;
					justify-content: center;
					align-items: center;
					padding: 0;
					margin: 0 50px;
					order: 2;
					li {
						list-style: none;
					}
					button {
						@include no-btn;
						width: 8px;
						height: 8px;
						background: #656565;
						border-radius: 50%;
						margin: 0 8px;
					}
					.slick-active button {
						background: #ffffff;
						border: 1.8px solid #ffffff;
					}
				}
			}
		}
	}
	.slider {
		position: relative;
		.slick {
			&-list {
				overflow: hidden;
			}
			&-track {
				display: flex;
				align-items: flex-start;
			}
			&-slide {
				margin: 0 12px;
			}
		}
	}
}
