@font-face {
	font-family: "Forum";
	src: url("../fonts/Forum-Regular.eot");
	src: local("☺"), url("../fonts/Forum-Regular.woff") format("woff"),
		url("../fonts/Forum-Regular.ttf") format("truetype"), url("../fonts/Forum-Regular.svg") format("svg");
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: "Segoe UI";
	src: url("../fonts/Segoe UI Italic.eot");
	src: local("☺"), url("../fonts/Segoe UI Italic.woff") format("woff"),
		url("../fonts/Segoe UI Italic.ttf") format("truetype"), url("../fonts/Segoe UI Italic.svg") format("svg");
	font-weight: normal;
	font-style: italic;
}
