.container {
	@include container;
}
.showcase {
	.article {
		padding: 47px 0 20px 0;
		margin-bottom: 10px;
		&__img {
			align-self: center;
		}

		&__title {
			margin-top: 65px;
		}

		&__description {
			margin-top: 6px;
		}

		&__price {
			margin-top: 6px;
		}
		&__title,
		&__description,
		&__price {
			margin-left: 30px;
		}
		@media (max-width: 799px) {
			width: 100%;
			height: 100%;
		}
		@media (max-width: 576px) {
			display: flex;
		}
	}
	&__row {
		display: flex;
		justify-content: space-between;
		@media (max-width: 1200px) {
			flex-wrap: wrap;
		}
		@media (max-width: 768px) {
			margin: 0 -10px;
		}
		@media (max-width: 576px) {
			flex-direction: column;
			margin: 0;
		}
	}

	&__column {
		flex: 0 1 33.333%;
		display: flex;
		flex-direction: column;
		align-items: center;
		@media (max-width: 799px) {
			justify-content: space-around;
			flex: 0 1 50%;
			padding: 0 10px;
		}
		@media (max-width: 576px) {
			padding: 0;
		}
		&_1 {
			align-items: flex-start;
		}
		&_2 {
			flex: 1 1 auto;
			@media (max-width: 799px) {
				display: none;
			}
			.wrapper {
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				margin-bottom: 10px;
				width: 100%;
				height: 100%;
				background: url(../img/showcase/main.jpg) 0 0/100% 100% no-repeat;
			}
			.article {
				background: transparent;
				justify-content: flex-end;
				margin-bottom: 0;
				&__title {
					a {
						color: #ffffff;
					}
				}
				&__description {
					a {
						color: #e7e7e7;
					}
				}
				&__price {
					a {
						color: #efc58b;
					}
				}
			}
		}
		&_3 {
			align-items: flex-end;
		}
	}
}
