@mixin font-face($name, $file, $weight: 400, $style: normal) {
	@font-face {
		font-family: "#{$name}";
		src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
			url("../fonts/#{$file}.woff") format("woff");
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
	}
}

@mixin bg {
	background: #272727;
}

@mixin btn_anim($scaleMax: 1.05, $scaleMin: 0.95) {
	transform-origin: center center;
	transition: all ease-out 240ms;

	&:hover {
		transform: scale(#{$scaleMax});
	}

	&:focus {
		outline: transparent;
	}

	&:focus-visible {
		transform: scale(#{$scaleMax}) translateY(-5%);
	}

	&:active {
		transform: scale(#{$scaleMin});
	}
}

@mixin no-btn($display: "inline-block") {
	padding: 0;
	margin: 0;
	border: 0;
	background-color: transparent;
	border-radius: 0;
	cursor: pointer;
	appearance: none;
	font-size: 0;
	display: #{$display};
}

@mixin transit($tr: 0.24) {
	transition: all #{$tr}s;
}

@mixin container($max-width: "120rem", $padding: "0") {
	width: 100%;
	max-width: #{$max-width};
	padding: 0 #{$padding};
	margin: 0 auto;
}
@mixin mainColor {
	color: #272727;
}
@mixin link {
	a {
		display: inline-block;
		text-decoration: none;
		color: #ffffff;
		&:hover {
			text-decoration: underline;
			@include btn_anim;
		}
	}
}
@mixin img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
