.map {
	display: flex;
	margin-bottom: 10px;
	@media (max-width: 576px) {
		flex-direction: column;
		margin-bottom: 10px;
	}
	&-column-one,
	&-column-two {
		flex: 0 0 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		@include bg;
		min-height: 604px;
		color: #ffffff;
		margin-bottom: 10px;
		@media (max-width: 576px) {
			min-height: 500px;
		}
	}
	&-column-one {
		flex-direction: column;
		@media (max-width: 992px) {
			padding: 20px;
		}
	}
	&-column-two {
		filter: grayscale(100%);
	}
	.wrapper {
		max-width: 386px;
	}
	&__title {
		font-family: Forum;
		font-size: 48px;
		line-height: 53px;
		letter-spacing: 0.03em;
		margin-bottom: 10px;
		@media (max-width: 992px) {
			font-size: 40px;
			line-height: 45px;
		}
	}
	&__description {
		font-family: Futura PT;
		font-weight: 300;
		font-size: 18px;
		line-height: 23px;
		margin-bottom: 55px;
		@media (max-width: 576px) {
			margin-bottom: 35px;
		}
	}
}
