.footer {
	.container {
		@include container;
		@include bg;
	}
	.row {
		padding: 80px 162px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;
		.column {
			flex: 0 1 100%;
			&_2 {
				margin-top: 90px;
				@media (max-width: 991px) {
					margin-top: 0px;
				}
			}
		}
		@media (max-width: 1200px) {
			padding: 50px;
		}
		@media (max-width: 575px) {
			padding: 15px;
		}
	}
	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin: 0 -8px;
		@media (max-width: 991px) {
			justify-content: flex-start;
		}
		&-link {
			font-family: Futura PT;
			font-weight: 450;
			font-size: 14px;
			line-height: 18px;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			padding: 0 8px;
			@media (max-width: 991px) {
				margin-bottom: 10px;
			}
			a {
				cursor: pointer;
				color: #ffffff;
				text-decoration: none;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 991px) {
			flex-wrap: wrap;
		}
		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
		}
		.wrapper {
			display: flex;
			@media (max-width: 991px) {
				margin-top: 20px;
			}
			@media (max-width: 575px) {
				flex-direction: column;
			}
		}
		&-social {
			.telephone {
				&__title {
					font-size: 14px;
					line-height: 18px;
					color: #ffffff;
				}
				&__link {
					font-size: 18px;
					line-height: 23px;
					margin-top: 15px;
					display: flex;
					flex-wrap: nowrap;
					a {
						cursor: pointer;
						color: #c2c2c2;
						text-decoration: none;
						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
			.messenger-icon {
				margin-top: 15px;
				&__item {
					margin-right: 20px;
				}
			}
		}
		&-download {
			margin-left: 80px;
			@media (max-width: 768px) {
				margin-left: 30px;
			}
			@media (max-width: 575px) {
				margin: 10px 0 0 0;
			}
			.form {
				&__title {
					font-size: 14px;
					line-height: 18px;
					color: #ffffff;
				}

				&__wrapper {
					margin-top: 15px;
				}

				&__input {
					width: 280px;
					padding-bottom: 5px;
					color: #c2c2c2;
					@media (max-width: 575px) {
						width: 100%;
					}
				}

				&__btn {
					top: unset;
					bottom: 10px;
				}
			}
			&-link {
				margin-top: 15px;
				a {
					display: inline-block;
					width: 90px;
					height: 30px;
					text-decoration: none;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}
