.container {
	@include container;
}
.contact-us {
	.row {
		display: flex;
		@media (max-width: 576px) {
			flex-direction: column;
		}
		.column {
			flex: 0 1 50%;
			&-1 {
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			&-2 {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				@media (max-width: 576px) {
					align-items: flex-start;
					padding: 15px;
				}
			}
		}
	}

	.telephone {
		&__title {
			@media (max-width: 768px) {
				font-size: 15px;
				line-height: 20px;
			}
		}

		&__link {
			margin-top: 30px;
		}
	}
	.form {
		margin-top: 105px;
		@media (max-width: 768px) {
			margin-top: 60px;
		}
		&__title {
			@media (max-width: 768px) {
				font-size: 15px;
				line-height: 20px;
			}
		}

		&__wrapper {
			margin-top: 30px;
		}

		&__input {
			font-size: 22px;
			line-height: 28px;
			color: #959595;
			background: transparent;
			border-bottom: 1.8px solid #272727;
		}

		&__btn {
			background: transparent;
			top: unset;
			bottom: 14px;
		}
	}
}
