.catalog {
	display: flex;
	margin-bottom: 10px;
	justify-content: space-between;
	@media (max-width: 576px) {
		flex-direction: column;
	}
	&-item {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin-bottom: 10px;
	}
	&__link {
		font-family: Futura PT;
		font-weight: 300;
		font-size: 24px;
		line-height: 31px;
		a {
			color: #ffffff;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__title {
		font-family: Forum;
		font-size: 64px;
		line-height: 71px;
		letter-spacing: 0.03em;
		margin-bottom: 20px;
		@media (max-width: 991px) {
			font-size: 55px;
		}
		@media (max-width: 768px) {
			font-size: 45px;
		}
		@media (max-width: 576px) {
			font-size: 50px;
		}
		a {
			color: #ffffff;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__divider {
		width: 130px;
		height: 0px;
		border: 1px solid #ffffff;
	}
}
.column-one,
.column-two {
	flex: 0 0 calc(50% - 10px);
	justify-content: center;
	align-items: center;
	height: 587px;
	@media (max-width: 576px) {
		flex: 0 0 100%;
		padding: 60px 0;
		margin-bottom: 10px;
	}
}
.column-one {
	background: url(../img/catalog/01.jpg) 0 0 / cover no-repeat;
}
.column-two {
	background: url(../img/catalog/02.jpg) 0 0 / cover no-repeat;
}
