.form {
	position: relative;
	&__title {
		font-family: Futura PT;
		font-size: 18px;
		line-height: 23px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		@include mainColor;
	}
	&__wrapper {
		display: block;
		position: relative;
	}
	&__input {
		position: relative;
		width: 100%;
		border: none;
		@include bg;
		border-bottom: 1.8px solid #ffffff;
		font-family: Futura PT;
		font-weight: 300;
		font-size: 18px;
		line-height: 23px;
		color: rgba(255, 255, 255, 0.4);
		outline: none;
	}
	&__btn {
		position: absolute;
		top: 0;
		right: 0;
		max-width: 28px;
		max-height: 15px;
		@include bg;
		button {
			width: 100%;
			height: 100%;
			@include no-btn;
			@include btn_anim;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}
