.telephone {
	&__title {
		font-family: Futura PT;
		font-size: 18px;
		line-height: 23px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		@include mainColor;
	}

	&__link {
		font-family: Futura PT;
		font-weight: 300;
		font-size: 22px;
		line-height: 28px;
		a {
			cursor: pointer;
			color: #959595;
			text-decoration: none;
			&:hover {
				transition: all 0.5s ease-in-out;
				text-decoration: underline;
			}
		}
	}
}
