.container {
	@include container;
}
.header {
	height: 1024px;
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;
	&__logo {
		max-width: 258px;
		max-height: 53px;
		@media (max-width: 576px) {
			display: none;
		}
		svg {
			width: 100%;
		}
		&-mobile {
			display: none;
			margin-right: 10px;
			@media (max-width: 576px) {
				display: inline-block;
			}
		}
	}

	&__content {
		h1 {
			font-family: Forum;
			font-weight: 400;
			font-size: 64px;
			line-height: 71px;
			letter-spacing: 0.03em;
			@include mainColor;
			margin-bottom: 45px;
			@media (max-width: 1072px) {
				font-size: 50px;
			}
			@media (max-width: 991px) {
				font-size: 45px;
				line-height: 50px;
			}
			@media (max-width: 768px) {
				font-size: 40px;
				line-height: 50px;
			}
			@media (max-width: 576px) {
				font-size: 40px;
				line-height: 35px;
			}
		}
	}

	&__content-link {
		display: flex;
		align-items: flex-end;
		padding: 0 0 0 125px;
		@media (max-width: 1200px) {
			padding: 0 0 0 20px;
		}
		@media (max-width: 576px) {
			padding: 0;
			margin-bottom: 45px;
		}
		.divider {
			width: 130px;
			height: 0;
			border: 1px solid #272727;
			margin-right: 38px;
			@media (max-width: 1200px) {
				width: 100px;
				margin-right: 15px;
			}
			@media (max-width: 991px) {
				margin-right: 20px;
				width: 80px;
			}
			@media (max-width: 768px) {
				display: none;
			}
		}
		a {
			font-family: Segoe UI;
			font-style: italic;
			font-weight: 300;
			font-size: 18px;
			line-height: 7px;
			text-decoration: none;
			@include mainColor;
			cursor: pointer;
			&:hover {
				transition: all 0.5s ease-in-out;
				text-decoration: underline;
			}
		}
	}

	&__language {
		&-link {
			font-family: Futura PT;
			font-weight: 400;
			font-size: 16px;
			line-height: 21px;
			letter-spacing: 0.05em;
			text-transform: uppercase;
			margin-right: 10px;
			cursor: pointer;
			text-decoration: none;
			color: #c4c4c4;
			&:active {
				@include mainColor;
			}
			&:hover {
				transition: all 0.2s ease-in-out;
				@include mainColor;
			}
		}
	}
	.header-column-one {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 60px 0 60px 162px;
		height: 100%;
		@media (max-width: 1400px) {
			padding: 60px 100px 60px 15px;
		}
		@media (max-width: 1200px) {
			padding: 60px 20px;
		}
		@media (max-width: 991px) {
			flex: 0 0 50%;
		}
		@media (max-width: 576px) {
			padding: 20px 20px 0 20px;
			margin-top: 95px;
		}
	}
	.header-column-two {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 562px;
		padding: 60px 162px 60px 0;
		background: black;
		@media (max-width: 1400px) {
			padding: 60px 100px 60px 0;
		}
		@media (max-width: 1200px) {
			padding: 60px 20px;
		}
		@media (max-width: 991px) {
			flex: 0 0 50%;
		}
		@media (max-width: 768px) {
			width: auto;
		}
		@media (max-width: 576px) {
			padding: 20px;
			flex-direction: row;
		}
	}
	.menu {
		position: fixed;
		top: 0;
		left: -100%;
		width: 100%;
		height: 100vh;
		padding: 40px 162px 0 162px;
		z-index: 10;
		visibility: hidden;
		overflow: auto;
		opacity: 0;
		transition: all 0.6s;
		@media (max-width: 991.98px) {
			padding: 40px 20px 0 20px;
		}
		&.active {
			left: 0;
			visibility: visible;
			opacity: 1;
		}
		@include bg;
		&__logo {
			max-width: 258px;
			max-height: 53px;
			svg {
				width: 100%;
			}
		}
		&__block {
			margin-top: 40px;
			padding: 27px 0;
			background: #2f2f2f;
			@media (max-width: 991.98px) {
				margin-top: 25px;
			}
		}
		&__list {
			list-style-type: none;
			display: flex;
			flex-wrap: wrap;
			padding: 0;
			margin: 0 -50px;
			@media (max-width: 1200px) {
				margin: 0 0 0 0;
			}
		}
		&__link {
			padding: 0 50px;
			font-family: Futura PT;
			font-weight: 450;
			font-size: 16px;
			line-height: 21px;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			margin-bottom: 15px;
			@media (max-width: 1200px) {
				padding: 0;
				margin-right: 15px;
			}
			a {
				display: inline-block;
				text-decoration: none;
				color: #ffffff;
				&:hover {
					text-decoration: underline;
					@include btn_anim;
				}
			}
		}
		&__catalog {
			display: flex;
			padding: 70px 0;
			border-bottom: 1.8px solid #2f2f2f;
			@media (max-width: 768.98px) {
				padding: 40px 0;
			}
			.column {
				margin-right: 100px;
				@media (max-width: 768.98px) {
					margin-right: 50px;
				}
				@media (max-width: 575.98px) {
					margin-right: 40px;
				}
			}
			&-item {
				font-family: Futura PT;
				font-size: 18px;
				line-height: 23px;
				letter-spacing: 0.06em;
				color: #ffffff;
			}
			&-title {
				font-size: 16px;
				line-height: 21px;
				text-transform: uppercase;
				margin-bottom: 30px;
			}

			&-link {
				margin-bottom: 10px;
				@include link;
				&_yellow {
					font-family: Segoe UI;
					font-style: italic;
					font-weight: 300;
					font-size: 16px;
					line-height: 21px;
					a {
						color: #efc58b;
					}
				}
			}
		}
		.menu {
			&__bottom {
				display: flex;
				padding: 70px 0;
				@media (max-width: 991.98px) {
					padding: 45px 0;
					flex-direction: column;
				}
				&-img {
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					width: 749px;
					height: 268px;
					background: url(../img/burgerMenu/01.jpg) 0 0 / cover no-repeat;
					padding: 30px;
					margin-right: 85px;
					@media (max-width: 1199.98px) {
						margin-right: 60px;
					}
					@media (max-width: 991.98px) {
						margin-right: 0;
					}
					@media (max-width: 767.98px) {
						width: 100%;
					}
					span {
						font-family: Futura PT;
						font-size: 18px;
						line-height: 23px;
						letter-spacing: 0.06em;
						color: #ffffff;
					}
					h3 {
						font-family: Forum;
						font-style: normal;
						font-weight: 400;
						font-size: 48px;
						line-height: 53px;
						letter-spacing: 0.03em;
						text-transform: uppercase;
						color: #ffffff;
						align-self: center;
					}
					a {
						position: relative;
						display: inline-block;
						text-decoration: none;
						padding-right: 50px;
						color: #ffffff;
						font-family: Futura PT;
						font-size: 18px;
						line-height: 23px;
						letter-spacing: 0.06em;
						align-self: flex-end;
						&:hover {
							text-decoration: underline;
							@include btn_anim;
						}
						&::after {
							content: "";
							position: absolute;
							top: 2px;
							right: 0;
							width: 30px;
							height: 15px;
							background: url(../../img/yandexMap/icons/arrow.svg) 0 0 / 100% no-repeat;
						}
					}
				}
				&-contacts {
					.wrapper {
						@media (max-width: 991.98px) {
							display: flex;
							padding: 15px 0 0 0;
						}
						@media (max-width: 576.98px) {
							flex-direction: column;
						}
						.footer {
							&__bottom-social {
								@media (max-width: 991.98px) {
									margin-right: 25px;
								}
								@media (max-width: 576.98px) {
									margin-right: 0;
									margin-bottom: 15px;
								}
							}

							&__bottom-download {
								margin: 45px 0 0 0;
								@media (max-width: 991.98px) {
									margin: 0 0;
								}
							}
						}
						.form {
							@media (max-width: 576.98px) {
								display: inline-block;
							}
							&__input {
								@media (max-width: 576.98px) {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}

	@media (max-width: 768px) {
		height: 100vh;
	}
	@media (max-width: 576px) {
		flex-direction: column-reverse;
		height: auto;
		.header-column-two {
			position: fixed;
			top: 0;
			z-index: 20;
			width: 100%;
		}
	}
}

.nav-menu {
	display: flex;
	justify-content: flex-end;
	position: relative;
	z-index: 50;
	&__item {
		margin-right: 40px;
		a {
			text-decoration: none;
		}
		&:nth-child(3) {
			&::after {
				content: "";
				position: absolute;
				top: 0;
				right: 60px;
				width: 10px;
				height: 23px;
				background: url(../img/1__block/icons/04.svg) center center no-repeat;
				@media (max-width: 576px) {
					top: 15px;
					right: 35px;
				}
			}
		}
		@media (max-width: 576px) {
			margin-right: 15px;
		}
	}
	.hamburger {
		position: relative;
		z-index: 50;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
		cursor: pointer;
		width: 30px;
		height: 16px;
		span {
			width: 100%;
			height: 0;
			border: 1px solid #ffffff;
			transition: all 0.6s;
		}
		&.active {
			span {
				&:nth-child(1) {
					transform: translateY(7px) rotate(-45deg);
				}
				&:nth-child(2) {
					display: none;
				}
				&:nth-child(3) {
					transform: translateY(-7px) rotate(45deg);
				}
			}
		}
	}
	@media (max-width: 576px) {
		align-items: center;
	}
}
