.article {
	display: flex;
	flex-direction: column;
	width: 400px;
	height: 400px;
	background: #ededed;
	&-big {
		align-items: flex-end;
		position: relative;
		background: transparent;
		width: unset;
		height: unset;
		max-width: 400px;
		max-height: 400px;
	}
	&__img {
		width: 260px;
		height: 180px;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		&-big {
			position: absolute;
			bottom: 310px;
			right: 259px;
			background: url(../img/1__block/ring.png) 0 0 / contain no-repeat;
			@media (max-width: 1200px) {
				right: 150px;
			}
			@media (max-width: 991px) {
				right: 75px;
			}
			@media (max-width: 768px) {
				position: relative;
				width: 100%;
				display: block;
				right: unset;
				bottom: unset;
				margin-bottom: 40px;
			}
		}
	}

	&__title {
		font-family: Futura PT;
		font-weight: 400;
		font-size: 16px;
		line-height: 21px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		a {
			@include mainColor;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
		&_big {
			a {
				color: #ffffff;
			}
		}
	}

	&__description {
		font-family: Futura PT;
		font-weight: 300;
		font-size: 18px;
		line-height: 23px;
		a {
			color: #757575;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
		&_big {
			a {
				color: #c2c2c2;
			}
		}
	}

	&__price {
		font-family: Segoe UI;
		font-style: italic;
		font-weight: 300;
		font-size: 18px;
		line-height: 24px;
		letter-spacing: 0.03em;
		a {
			color: #e2b67a;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
		&_big {
			a {
				color: #efc58b;
			}
		}
	}
	@media (max-width: 576px) {
		display: none;
	}
}
